body {
  margin: 0;
  padding: 0;
  background: #f5f5f5;
}

.App {
  height: 100%;
}

.navBarItems{
   display: flex;
   flex-wrap: wrap;
   position: fixed;
   align-items: center;
   width: 100%;
   background: #039BE5;
   min-height: 58px;
   height: auto;
   font-size: 1.2em;
   margin-bottom: auto;
}
.navItem{
   margin-right:1.2em;
   padding-left: 1.2em;
   text-decoration: none;
   color: white;
}
ul {
  padding: 0;
  list-style: none;
  padding: 0;
  margin: 0;
}
.posLeft{
   margin-right: auto;
   padding: 0 5px;
}
.navBarItems a:hover{
   color: #fa923f;
   cursor: pointer;
}
.navBarItems a:active{
   color: #fa923f;
   cursor: pointer;
}

.searchBox{
	background: #fff;
	text-align: center;
	font-size: 14pt;
	width: 25%;
	height: 30px;
	margin-top: 80px;
	border: none;
	outline: none;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.searchboxContainer{
	display: flex;
	justify-content: center;
}
::-webkit-input-placeholder{
	text-align: center;
	color: black;
}
::-moz-placeholder{
	text-align: center;
	color: black;
}
::-ms-input-placeholder{
	text-align: center;
	color: black;
}
::placeholder{
	text-align: center;
	color: black;
}

.home{
	display: flex;
	flex-flow: column;
	align-items: center;
	justify-content: center;
}
.main{
	display: flex;
	flex-flow: column;
	justify-content: center;
	align-items: center;
	margin-top: 70px;
}
.header{
	text-align: center;
	color: #05b4e4;
	font-size: 36px;
	text-transform: uppercase;
	margin: 0;
}
.image{
	width: 55%;
	min-width: 350px;
	height: auto;
}
.innerText{
	text-align: center;
	width: 80%;
	color: #333;
	font-size: 28px;
}

.card{
	display: flex;
	flex-flow: column;
	justify-content: center;
	align-items: center;
	margin: 20px;
	padding: 5px;
	background: #fff;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.link{
	-webkit-transition: -webkit-transform .2s;
	transition: -webkit-transform .2s;
	transition: transform .2s;
	transition: transform .2s, -webkit-transform .2s; /* Animation */
	text-decoration: none;
}
.formatImage{
	width: 250px;
	height: auto;
	border-radius: 1em;
}
.textStyle{
	padding-left: 10px;
	font-weight: bold;
	font-size: 20px;
	color: #05b4e4;
}
.formatCard{
	height: auto;
}

.cardsContainer{
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}
.messages{
	font-family: 'Notable', 'san-serif';
	font-weight: 200;
  	color: #05b4e4;
  	height: auto;
  	font-size: 50px;
}

.icons{
	display: flex;
	flex-direction: column;
	text-align: center;
	color: black;
	justify-content: center;
	margin: 0 10px 0 10px;
}

